.card {
  background: #fff;
  position: absolute;
  border-radius: 2px;
  height: 130px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  transition: all 0.05s;
}
.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0 6px 14px #99999982, 0px 4px 20px #999999c2;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}

.bg-1 {
  background: var(--red);
}
.bg-2 {
  background: var(--teal);
}
.bg-3 {
  background: var(--blue);
}
.bg-4 {
  background: var(--orange);
}

.card > .img-container-red {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #a33535;
}
.card > .img-container > img {
  width: 80%;
}

.card:hover {
  cursor: pointer;
}

.score {
  position: relative;
  max-width: 90%;
  margin: 0 auto;
}

.score p {
  font-size: 24px;
  width: 350px;
  margin: 0 auto;
}
.current-score {
  float: left;
}
.high-score {
  float: right;
  text-align: right;
}
#correct,
#incorrect {
  font-size: 26px;
  font-weight: 800;
  transition: -webkit-filter 0.1s;
  transition: filter 0.1s;
  transition: filter 0.1s, -webkit-filter 0.1s;
}
#correct {
  color: #55e284;
  text-shadow: 1px 1px 3px #296e4e, -1px -1px #296e4e, -1px 1px #296e4e,
    1px -1px #296e4e;
  padding-left: 10px;
}
#incorrect {
  color: #e25555;
  text-shadow: 1px 1px 3px #6e2929, -1px -1px #6e2929, -1px 1px #6e2929,
    1px -1px #6e2929;
  padding-right: 10px;
}

.wrapper {
  padding-top: 50px;
  background: #eceff1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}
body {
  background: radial-gradient(
    ellipse at center,
    #d5e5e8 32%,
    #d5e5e8 44%,
    #a1e2d4 100%
  );
}

.title {
  font-size: 48px;
  font-weight: 800;
  background: -webkit-linear-gradient(#821a15, #e73222);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px #f5ed61;
  letter-spacing: -4px;
}
header {
  text-align: center;
  padding: 50px;
  width: 100%;
}
.logo {
  position: relative;
  width: 360px;
  top: 0px;
}
.ship {
  position: absolute;
  right: calc(51% - 120px);
  top: 118px;
  width: 220px;
  z-index: -1;
}
header p {
  font-size: 14px;
  font-style: italic;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

